import AbstractPic from '../images/AbstractPic.jpeg';
import Sidebar from '../components/Sidebar';
import Aside from '../components/Aside/Aside';
import Punchline from '../components/punchline';
import { Card, CardBody, CardTitle, Col, Row } from 'reactstrap';

const Article = () =>
(
    <main className="backpage-content col-sm-8 col-md-9">
    	<article>
            <div className="backpage-entry-content">
            	<p>Creating Mobility Apps for Foce.com ecosystem</p>
            	<ol>
            		<li><strong>React Native</strong> Learn React Native to start with.</li>
					<li><strong>Mobility Apps Design</strong>Technology blog to build engaging mobile application - coming soon!</li>
				</ol>
			</div>
        </article>
     </main>
);

const AbstractDesign = () =>
(
    <Card className="backpage-entry-content">
		<img src={AbstractPic} alt="Abstract Pic" />
    </Card>
);

const MobilityPage = () => (
	<div className="animated fadeIn">
        <Punchline tag="Abstract Design by Tanishq Shandilya" underline={false}/>
		<Row>
			<Col className="backpage-content">
                <article>
                    <AbstractDesign/>
					
                </article>
            </Col>
    		<Aside/>
		</Row>
	</div>
);

export default MobilityPage;



